$bigFont: 4rem;
$secondFont: 3rem;
$thirdFont: 2.5rem;
$fourthFont: 2rem;

$mainColor: rgba(var(--bs-dark-rgb));
$secondColor: #0f0f0f;
$thirdColor: #9B3922;
$fourthColor: #F2613F;
$fontColor: white;
$buttonColor: #373737;

body{
    background-color: $secondColor; color: $fontColor;
    position: relative;
    
}	

header, footer {
    background-color: $mainColor;
}

header {
    position: sticky;
    top: 0;
    z-index: 1;
    height: 8%;
    opacity: 0.9;
    width: 100%;
}

footer {
    height: 50px;
    position: absolute;
    bottom: 0;
    width: 100%;
    margin-top: 2.5%;
    a {
        color: $thirdColor;
        
    }

}

a {
    color: $thirdColor;

    &:hover {
        text-decoration: none;
        color: $fourthColor;
    }
}



.hl01  { font-size:$bigFont; font-family:Century Schoolbook L; text-align:center; }
.hl02  { font-size:$secondFont; font-family:Century Schoolbook L; text-align:center; }
.hl03  { font-size:$thirdFont; font-family:Century Schoolbook L; text-align:center; }
.hl04  { font-size:$fourthFont; font-family:Century Schoolbook L; text-align:center; }
.txt01 { font-size:$bigFont; font-family:Century Schoolbook L; text-align:left; }
.txt02 { font-size:$secondFont; font-family:Century Schoolbook L; text-align:left; }
.txt03 { font-size:$thirdFont; font-family:Century Schoolbook L; text-align:left; }
.txt04 { font-size:$fourthFont; font-family:Century Schoolbook L; text-align:left; }
.txtR01 { font-size:$bigFont; font-family:Century Schoolbook L; text-align:right; }
.txtR02 { font-size:$secondFont; font-family:Century Schoolbook L; text-align:right; }
.txtR03 { font-size:$thirdFont; font-family:Century Schoolbook L; text-align:right; }
.txtR04 { font-size:$fourthFont; font-family:Century Schoolbook L; text-align:right; }
.txtft { font-size:$thirdFont; font-family:Century Schoolbook L; text-align:right; background-color:Transparent;}

.mainHeader {
    padding-top: 6%;
    padding-bottom: 6%;
}

.welcomeHeader {
    padding-bottom: 6%;
}

.box { 
	border: 5px solid $mainColor; 
    border-radius: 25px;
	padding: 40px 20px;
    margin-top: 5%;
    margin-bottom: 15%;
    background-color: $thirdColor;
}

.footerContent, .headerContent{
    margin: auto;
    width: 100%;
    text-align: center;
    list-style-type: none;
    margin: 0;
    padding: 0;

}

.footerContent {
    height: 100%;
}

.headerContent {
    padding: 0;
    height: 100%;
    overflow: hidden;
}
    
.footerItem {
    color: $secondColor;
    padding: 0 40px;
    margin: auto;
    display: inline-block;
    a {
        text-decoration: none;
    }
    
}

.navbar-nav-custom {
    width: 100%;
    padding: 0 20%;
}
.nav-item {
    width: 20%;
}
.navbar {
    height: 100%;
}

.headerItem {

    height: 100%;
    align-self: center;
    color: $thirdColor;
    font-size:16pt; 
    font-family:Bitstream Charter; 
    
    &:hover {
        color: $fourthColor;
    }
}


.imgRobo{
    position: absolute;
    bottom: 100;

}

.kirembelImg {
    height: auto;
    max-width: 100%;
}

.linkbutton { 
}

.curentPage {
    text-decoration: underline;
}

.navbar-nav .navbar-center {
    position: absolute;
    left: 50%;
    transform: translatex(-50%);
}		
.spaceBottom {
    margin-bottom: 10%;
}
.littleSpaceBottom {
    margin-bottom: 5%;
}

.custom-border {
    border: 4px solid $mainColor;
    border-radius: 5px;
}

.line {
    height: 2px;
    background: $fontColor;
    margin-bottom: 10%;
}

.littleSpaceTop {
    margin-top: 5%;
}
.spaceTop {
    margin-top: 10%;
}

.defaultImg {
    width: 100%;
}

.galerieRow {
    --bs-gutter-x: 0;
}